<template>
    <v-container>

        <h4>{{knownLanguagesTitle}}</h4>
        <v-combobox
                v-model="chips_known_languages"
                :items="items_known_languages"
                chips
                type="button"
                clearable
                :label="knownLanguagesLabel"
                multiple
                solo
        >
            <template v-slot:item="{ index, item }">
                <v-chip
                        color="white"
                        block
                >
                    <v-avatar left>
                        <v-img :src="item.img"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>

            <template v-slot:selection="{ attrs, item, select, selected }">
                <v-chip
                        v-bind="attrs"
                        :input-value="selected"
                        close
                        color="primary"
                        @click="select"
                        @click:close="removeKnownLanguage(item)"
                >
                    <v-avatar left>
                        <v-img :src="item.img"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>
        </v-combobox>

        <h4>{{languagesToLearnTitle}}</h4>
        <v-combobox
                v-model="chips_languages_to_learn"
                :items="items_languages_to_learn"
                chips
                clearable
                type="button"
                :label="languagesToLearnLabel"
                multiple
                solo
        >
            <template v-slot:item="{ index, item }">
                <v-chip
                        color="white"
                        block
                >
                    <v-avatar left>
                        <v-img :src="item.img"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>

            <template v-slot:selection="{ attrs, item, select, selected }">
                <v-chip
                        v-bind="attrs"
                        :input-value="selected"
                        close
                        color="primary"
                        @click="select"
                        @click:close="removeLanguageToLearn(item)"
                >
                    <v-avatar left>
                        <v-img :src="item.img"></v-img>
                    </v-avatar>
                    <strong>{{ item.text }}</strong>&nbsp;
                </v-chip>
            </template>
        </v-combobox>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex';

    // Selector de lenguages conocidos y a practicar
    // @group Auxiliares
    export default {
        name: "YourLanguagesSelector",
        props: {
            // Indicacin de si mostrar o no el campo para introducir el nombre de usuario
            knownLanguagesInitialValue: {
                type: [Array],
                required: true,
            },
            // Indicacin de si mostrar o no el campo para introducir el nombre de usuario
            languagesToLearnInitialValue: {
                type: [Array],
                required: false,
            }
        },
        data() {
            return {
                snackbar: false,
                snackbarText: '',
                chips_known_languages: [],
                chips_languages_to_learn: [],
                items_known_languages: this.$store.getters['user/getLanguageOptionsAsArray'].filter(function (lang) {
                    return lang.id != "all";
                }),
                items_languages_to_learn: this.$store.getters['user/getLanguageOptionsAsArray'].filter(function (lang) {
                    return lang.id != "all";
                }),
                languagesToLearnLabel: null,
                languagesToLearnTitle: null,
                knownLanguagesLabel: null,
                knownLanguagesTitle: null,
                closeText: null
            }
        },
        methods: {
            ...mapActions('user', ['setLanguages']),

            // @vuese
            // Elimina el idioma seleccionado por el usuario como 'a practicar' de las opciones de idiomas 'conocidos'
            // @arg El parmetro es el idioma seleccionado
            removeKnownLanguage(item) {
                this.chips_known_languages.splice(this.chips_known_languages.indexOf(item), 1)
                this.chips_known_languages = [...this.chips_known_languages]
            },

            // @vuese
            // Elimina el idioma seleccionado por el usuario como 'conocido' de las opciones de idiomas 'a practicar'
            // @arg El parmetro es el idioma seleccionado
            removeLanguageToLearn(item) {
                this.chips_languages_to_learn.splice(this.chips_languages_to_learn.indexOf(item), 1)
                this.chips_languages_to_learn = [...this.chips_languages_to_learn]
            },

            // @vuese
            // Evala la validez de los lenguages seleccionados
            validLanguages() {
                let data;

                if (this.chips_known_languages.length > 0 && this.chips_languages_to_learn.length > 0) {
                    let knownLanguagesIDs = [];
                    let languagesToLearnIDs = [];

                    for (let i = 0; i < this.chips_known_languages.length; i++) {
                        knownLanguagesIDs.push(this.chips_known_languages[i].id);
                    }
                    for (let i = 0; i < this.chips_languages_to_learn.length; i++) {
                        languagesToLearnIDs.push(this.chips_languages_to_learn[i].id);
                    }

                    data = {
                        'isValid': true,
                        'knownLanguages': knownLanguagesIDs,
                        'languagesToLearn': languagesToLearnIDs
                    };
                } else {
                    data = {
                        'isValid': false
                    };
                }

                // Se genera al actualizar los idiomas seleccionados
                // @arg El parmetro  es un Objeto que representa los idiomas seleccionados
                this.$emit('dataValidityUpdate', data);

            },

            // @vuese
            // Inicializa los idiomas conocidos
            filterItemsKnownLanguages: function () {
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_known_languages = this.$store.getters['user/getLanguageOptionsAsArray'].filter(function (lang) {
                    return lang.id != "all";
                }).filter(function (value) {
                    let toInclude = true;
                    for (let i = 0; i < self.chips_languages_to_learn.length; i++) {
                        if (self.chips_languages_to_learn[i].id == value.id) {
                            toInclude = false;
                            break;
                        }
                    }
                    if (toInclude) {
                        return value;
                    }
                })
            },

            // @vuese
            // Inicializa los idiomas a practicar
            filterItemsLanguagesToLearn: function () {
                // eslint-disable-next-line no-unused-vars
                let self = this;
                this.items_languages_to_learn = this.$store.getters['user/getLanguageOptionsAsArray'].filter(function (lang) {
                    return lang.id != "all";
                }).filter(function (value) {
                    let toInclude = true;
                    for (let i = 0; i < self.chips_known_languages.length; i++) {
                        if (self.chips_known_languages[i].id == value.id) {
                            toInclude = false;
                            break;
                        }
                    }
                    if (toInclude) {
                        return value;
                    }
                })
            }

        },
        watch: {
            // @vuese
            // Observa los idiomas seleccionados como conocidos por el usuario y realiza las actualizaciones oportunas
            chips_known_languages: function () {
                this.validLanguages();
                this.filterItemsLanguagesToLearn();
            },
            // Observa los idiomas seleccionados como 'a practicar' por el usuario y realiza las actualizaciones oportunas
            chips_languages_to_learn: function () {
                this.validLanguages();
                this.filterItemsKnownLanguages();
            }
        },
        created() {
            this.languagesToLearnLabel = this.$i18n.t('messages.TEXT_LANGUAGES_YOU_WANT_TO_IMPROVE');
            this.languagesToLearnTitle = this.$i18n.t('messages.TEXT_LANGUAGES_YOU_WANT_TO_PRACTICE');
            this.knownLanguagesLabel = this.$i18n.t('messages.TEXT_LANGUAGES_YOU_SPEAK_FLUENTLY');
            this.knownLanguagesTitle = this.$i18n.t('messages.TEXT_MOTHER_LANGUAGES');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
        },
        mounted() {
            if (this.knownLanguagesInitialValue && this.knownLanguagesInitialValue.length > 0) {
                for (let i = 0; i < this.knownLanguagesInitialValue.length; i++) {
                    let id = this.knownLanguagesInitialValue[i];
                    let language = {
                        'id': id,
                        ...this.$store.getters['user/getLanguageFromLanguageOptionsByID'](id)
                    }

                    this.chips_known_languages.push(language);
                }
            }

            if (this.languagesToLearnInitialValue && this.languagesToLearnInitialValue.length > 0) {
                for (let i = 0; i < this.languagesToLearnInitialValue.length; i++) {
                    let id = this.languagesToLearnInitialValue[i];
                    let language = {
                        'id': id,
                        ...this.$store.getters['user/getLanguageFromLanguageOptionsByID'](id)
                    }
                    this.chips_languages_to_learn.push(language);
                }
            }
        }
    }
</script>

