<template>
    <v-container style="height: 100%; padding-right: 0px; padding-left: 0px; position: relative; width: 100%"
                 align="center">

        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{editProfileText}}</v-toolbar-title>
                </v-col>
                <v-col class="text-right" style="padding-top: 0; padding-bottom: 0">
                    <v-btn class="app-bar-button" small tile style="display: inline;" text dark
                           @click="updateUserProfileData">
                        {{saveText}}
                    </v-btn>
                </v-col>
            </v-row>

        </v-app-bar>

        <div style="margin-top: 60px"></div>

        <v-container style="padding-bottom: 18px; padding-top: 20px">
            <h4>{{profileImgText}}</h4>
            <v-card class="text-center justify-center pa-4"
                    style="padding: 10px!important;">
                <profile-image-selector
                        size="0.5"
                        :image-preview-initial-value="imagePreview"
                        @newImageSelected="newImageSelected($event)">
                </profile-image-selector>
            </v-card>
        </v-container>
        <v-container style="padding-bottom: 18px; padding-top: 20px">
            <h4>{{profileInfoText}}</h4>
            <v-card class="text-center justify-center pa-4">
                <profile-info-selector
                        :description-initial-value="descriptionPreview"
                        :gender-initial-value="genderPreview"
                        :birth-date-initial-value="birthDatePreview"
                        @dataValidityUpdate="isProfileInfoDataValid($event)">
                </profile-info-selector>
            </v-card>
        </v-container>
        <YourLanguagesSelector
                :known-languages-initial-value="knownLanguagesPreview"
                :languages-to-learn-initial-value="languagesToLearnPreview"
                @dataValidityUpdate="isLanguagesDataValid($event)">
        </YourLanguagesSelector>

        <!-- Error snackbar -->
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
        <!--End snackbar-->
    </v-container>
</template>

<script>
    import YourLanguagesSelector from "@/components/YourLanguagesSelector";
    import ProfileInfoSelector from "@/components/ProfileInfoSelector";
    import ProfileImageSelector from "@/components/ProfileImageSelector";
    import AuxMethods from "../../store/AuxMethods";
    import {mapActions} from "vuex";

    // Pgina de edicin del perfil del usuario autenticado
    // @group Perfil
    export default {
        name: "ProfileEdit",
        components: {
            YourLanguagesSelector,
            ProfileInfoSelector,
            ProfileImageSelector
        },
        data() {
            return {
                validForm: false,
                profileInfoText: null,
                username: null,
                imagePreview: this.$store.getters['user/getProfileImg'],
                imageUpload: this.imagePreview,
                descriptionPreview: this.$store.getters['user/getDescription'],
                description: this.descriptionPreview,
                genderPreview: this.$store.getters['user/getGender'],
                gender: this.genderPreview,
                birthDate: this.birthDatePreview,
                knownLanguagesPreview: this.$store.getters['user/getKnownLanguages'],
                knownLanguages: this.knownLanguagesPreview,
                languagesToLearnPreview: this.$store.getters['user/getLanguagesToLearn'],
                languagesToLearn: this.languagesToLearnPreview,
                saveText: null,
                editProfileText: null,
                snackbar: false,
                snackbarText: '',
                closeText: null,
                profileImgText: null,
                isNewProfileInfoValid: true,
                isNewLanguagesValid: true
            }
        },
        methods: {
            ...mapActions('user', ['updateProfile', 'setProfileImg']),

            // @vuese
            // Comprueba la validez de los datos introducidos en el componente ProfileInfoSelector
            isProfileInfoDataValid: function (data) {
                if (data.isValid) {
                    this.username = data.username;
                    this.birthDate = data.birthDate;
                    this.gender = data.gender;
                    this.description = data.description;
                }

                this.isNewProfileInfoValid = data.isValid;
            },

            // @vuese
            // Comprueba la validez de los lenguages seleccionados en el componente YourLanguagesSelector
            isLanguagesDataValid: function (data) {
                if (data.isValid) {
                    this.languagesToLearn = data.languagesToLearn;
                    this.knownLanguages = data.knownLanguages;
                }

                this.isNewLanguagesValid = data.isValid;
            },

            // @vuese
            // Actualiza la imagen seleccionada en el componente ProfileImageSelector
            newImageSelected: function (image) {
                this.imageUpload = image;
            },

            // @vuese
            // Guarda los datos de perfil actualizados por el usuario
            updateUserProfileData() {
                let data = {};
                let isInfoToUpdate = false;

                if (this.isNewProfileInfoValid && this.isNewLanguagesValid) {
                    if (this.description && this.description != this.descriptionPreview) {
                        data = {
                            'description': this.description,
                            ...data
                        };
                        isInfoToUpdate = true;

                        console.log("description to update.")
                    }

                    if (this.gender && this.gender != this.genderPreview) {
                        data = {
                            'gender': this.gender.id,
                            ...data
                        };
                        isInfoToUpdate = true;

                        console.log("gender to update.")
                    }
                    let pDate = new Date(this.birthDatePreview);
                    pDate.setDate(pDate.getDate() - 1);

                    if (this.birthDate && new Date(this.birthDate).toISOString() !== pDate.toISOString()) {
                        data = {
                            'birthDate': this.birthDate,
                            ...data
                        };
                        isInfoToUpdate = true;

                        console.log("birthDate to update.")
                    }

                    if (this.knownLanguages && !AuxMethods.arraysContainSameElements(this.knownLanguages, this.knownLanguagesPreview)) {
                        data = {
                            'knownLanguages': this.knownLanguages,
                            ...data
                        };
                        isInfoToUpdate = true;

                        console.log("knownLanguages to update.")
                    }
                    if (this.languagesToLearn && !AuxMethods.arraysContainSameElements(this.languagesToLearn, this.languagesToLearnPreview)) {
                        data = {
                            'languagesToLearn': this.languagesToLearn,
                            ...data
                        };
                        isInfoToUpdate = true;

                        console.log("languagesToLearn to update.")
                    }


                    if (this.imageUpload != this.imagePreview && this.imageUpload != null) {
                        console.log("imageUpload to update.")
                        isInfoToUpdate = true;
                    }

                    if (isInfoToUpdate) {
                        let resultUpdateProfile = this.updateProfile(data);
                        let self = this;

                        resultUpdateProfile.then(() => {
                            if (self.imageUpload != self.imagePreview && self.imageUpload != null) {

                                let resultSetProfileImg = self.setProfileImg(self.imageUpload);
                                resultSetProfileImg.then(() => {
                                    console.log("imageUpload updated.")
                                    this.$router.replace({name: 'profileHome'});
                                }).catch((error) => {
                                    console.log("Error on imageUpload to update.")
                                    console.error(error)
                                    this.$router.replace({name: 'profileHome'});
                                });
                            } else {
                                this.$router.replace({name: 'profileHome'});
                            }

                        })
                            .catch((error) => {
                                this.snackbarText = error;
                                this.snackbar = true;
                            });
                    } else {
                        this.$router.replace({name: 'profileHome'});
                    }
                } else {
                    this.snackbarText = this.askCompleteAllFields;
                    this.snackbar = true;
                }
            }
        },
        created() {
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.profileInfoText = this.$i18n.t('messages.TEXT_PROFILE_INFO');
            this.saveText = this.$i18n.t('messages.TEXT_SAVE');
            this.editProfileText = this.$i18n.t('messages.TEXT_EDIT_PROFILE');
            this.profileImgText = this.$i18n.t('messages.TEXT_PROFILE_IMAGE');
        },
        computed: {
            birthDatePreview: function () {
                let originalDate = this.$store.getters['user/getBirthDate'];
                let date = new Date(originalDate);
                date.setDate(date.getDate() + 1);
                return date;
            },
            askCompleteAllFields: function () {
                return this.$i18n.t('messages.TEXT_FILL_FIELDS_CORRECTLY');
            },
        }
    }
</script>

<style scoped>
    .app-bar-button {
        height: 100% !important;
        min-width: unset !important;
    }
</style>